<template>
  <el-row id="registration" :gutter="0">
    <!--选择显示页数和搜索框内容组件-->
    <select-and-search :c="c" :searching="searching" @sizeChange="sizeChange" @search="search"
                       @searchingChange="searchingChange"></select-and-search>
    <!--标签页表格-->
    <el-col :span="24">
      <el-tabs v-model="tabNum" type="card">
        <el-tab-pane label="家属注册" name="first">
          <el-table
            :data="registrations"
            border
            stripe
            style="width: 100%">
            <el-table-column
              prop="name"
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="phone"
              label="电话">
            </el-table-column>
            <el-table-column
              prop="uuid"
              label="身份证">
            </el-table-column>
            <el-table-column
              label="申请时间">
              <template slot-scope="scope">
                {{scope.row.createdAt | Date}}
              </template>
            </el-table-column>
            <el-table-column
              prop="prisonerNumber"
              label="囚号">
            </el-table-column>
            <el-table-column
              prop="relationship"
              label="关系">
            </el-table-column>
            <el-table-column
              prop="status"
              label="申请状态">
              <template slot-scope="scope">
                {{scope.row.status | registrationsStatus}}
              </template>
            </el-table-column>
            <el-table-column
              label="操作">
              <template slot-scope="scope">
                <el-button v-if="scope.row.status == 'PENDING'"
                           size="mini"
                           @click="handleAuthorization(scope.row.id)">授权
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-col>
    <!--分页组件-->
    <pagination :total="registrationsTotal" :pageSize="pagination.rows" :currentPage="pagination.page"
                @currentChange="currentChange"></pagination>
    <!--家属信息授权弹出框-->
    <el-dialog title="授权" :visible.sync="dialogVisible">
      <!--内层的对话框-->
      <el-dialog
        width="30%"
        :visible.sync="innerVisible"
        append-to-body>
        <!--<img style="max-width:100%" :src="_$baseUrl + imgSrc" alt="">-->
        <!--<img style="max-width:100%" :src="_$agency + imgSrc" alt="">-->
        <img src="../../assets/images/default.jpg" alt="">
      </el-dialog>
      <el-row :gutter="0" v-show="authRegistrationsResult.code">
        <el-col :span="24">
          <el-alert v-if="authRegistrationsResult.code == 200"
                    title="授权成功"
                    type="success"
                    :description="authRegistrationsResult.msg"
                    show-icon
                    :closable="false">
          </el-alert>
          <el-alert v-else
                    title="授权失败"
                    type="danger"
                    :description="authRegistrationsResult.msg"
                    show-icon
                    :closable="false">
          </el-alert>
        </el-col>
      </el-row>
      <el-row :gutter="0">
        <el-col :span="24">
          <el-col v-for="imgSrc,$index in uuidImages" :span="6" :offset="$index == 0?2:1" :key="$index">
            <!--<img :src='_$baseUrl + imgSrc' alt="" @click="amplifyImage(imgSrc)">-->
            <!--<img :src='_$agency + imgSrc' alt="" @click="amplifyImage(imgSrc)">-->
            <img src="../../assets/images/default.jpg" alt="" @click="amplifyImage(imgSrc)">
          </el-col>
        </el-col>
      </el-row>
      <el-row :gutter="0" v-show="!authRegistrationsResult.code">
        <el-col :span="24" v-if="showRemarks" class="refuse-reason">
          <el-col :span="24">
            <p>请选择驳回原因</p>
          </el-col>
          <el-select v-model="authorization.remarks">
            <el-option v-for="remark,index in remarks"
                       :value="remark"
                       :label="remark"
                       :key="index">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <el-button plain @click="agreeAuthorization(agreeText)">{{agreeText}}</el-button>
        </el-col>
        <el-col :span="24">
          <el-button plain @click="disagreeAuthorization(disagreeText)">{{disagreeText}}</el-button>
        </el-col>
        <el-col :span="24">
          <el-button type="danger" plain @click="dialogVisible = false">关闭</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </el-row>
</template>

<script>
  import {mapActions, mapMutations, mapGetters} from 'vuex'
  import SelectAndSearch from '@/components/Select-And-Search/Select-And-Search'
  import Pagination from '@/components/Pagination/Pagination'
  export default {
    data() {
      return {
        breadcrumb: ['主页', '家属注册管理'],//面包屑数组
        tabNum: 'first',
        c: 'registrations',
        pagination: {
          rows: 10,//每页显示记录条数
          page: 1//当前显示第几页
        },
        searching: {
          name: '',//家属姓名
          prisonerNumber: '',//囚号
          uuid: ''//身份证号
        },
        dialogVisible: false,//弹出框的显示和隐藏
        innerVisible: false,//内层弹框的显示和隐藏
        agreeText: '同意',
        disagreeText: '不同意',
        authorization: {
          remarks: '',//授权评语
          status: ''//授权状态
        },
        showRemarks: false,//是否显示拒绝家属注册理由
        imgSrc: ''//放大查看家属注册的照片地址
      }
    },
    computed: {
      //映射getters方法获取state状态
      ...mapGetters({
        registrations: 'registrations',//获取家属注册的注册信息列表
        uuidImages: 'uuidImages',//获取家属注册时的照片数组
        registrationsTotal: 'registrationsTotal',//获取家属注册时的总记录数
        authRegistrationsResult: 'authRegistrationsResult',//获取给家属授权的授权结果
        remarks: 'remarks'//获取拒绝家属注册的理由
      })
    },
    methods: {
      //映射mutations方法
      ...mapMutations({
        breadCrumb: 'breadCrumb',//设置家属注册页面的面包屑信息
        setAuthRegistrationsResult: 'setAuthRegistrationsResult'//设置家属注册授权信息
      }),
      //映射actions方法
      ...mapActions({
        getRegistrations: 'getRegistrations',//获取家属注册列表
        getUuidImage: 'getUuidImage',//获取对应家属得照片地址
        searchAction: 'searchAction',//获取带搜索条件的家属注册列表
        authorizeRegistrations: 'authorizeRegistrations'//家属注册信息授权
      }),
      //每页条数发生变化时执行的方法
      sizeChange(rows){
        this.pagination.page = 1;
        this.pagination.rows = rows;
        this.change();
      },
      //当前页发生变化时执行的方法
      currentChange(page){
        this.pagination.page = page;
        this.change();
      },
      //根据是否有搜索内容调用不同的接口
      change(){
        this.getRegistrations({...this.searching, ...this.pagination});
      },
      //点击搜索时执行的方法
      search(searching){
        this.pagination.page = 1;
        this.searching = searching;
        this.getRegistrations({...this.searching, ...this.pagination});
      },
      //监听搜索框的内容变化
      searchingChange(searching){
        this.searching = searching;
      },
      //点击授权时执行的方法
      handleAuthorization(id) {
        this.showRemarks = false;
        this.dialogVisible = true;
        this.authorization.remarks = '您的身份信息错误';
        this.authorizeId = id;
        this.agreeText = '同意';
        this.disagreeText = '不同意';
//        this.getUuidImage(id);
        this.setAuthRegistrationsResult({});//重置家属注册授权结果
      },
      //点击同意或者确定申请通过执行的方法
      agreeAuthorization(agreeText){
        if (agreeText === '同意') {
          this.agreeText = '确定申请通过？';
          this.disagreeText = '返回';
        } else {
          if (agreeText === '提交')
            this.authorization.status = 'DENIED';
          else {
            this.authorization.remarks = '';
            this.authorization.status = 'PASSED';
          }
          this.authorizeRegistrations({...this.authorization, id: this.authorizeId});
        }
      },
      //点击不同意或者返回执行的方法
      disagreeAuthorization(disagreeText){
        if (disagreeText === '返回') {
          this.showRemarks = false;
          this.disagreeText = '不同意';
          this.agreeText = '同意';
        } else {
          this.showRemarks = true;
          this.agreeText = '提交';
          this.disagreeText = '返回';
        }
      },
      //图片放大执行的方法
      amplifyImage(imgSrc){
        this.innerVisible = true;
        this.imgSrc = imgSrc;
      }
    },
    mounted(){
      //将面包屑数组传递给Content组件
      this.breadCrumb(this.breadcrumb);
      //获取家属注册信息列表
      this.getRegistrations(this.pagination);
    },
    components: {
      SelectAndSearch,
      Pagination
    }
  }
</script>

<style type="text/stylus" lang="stylus" scoped>
  white = #fff
  #registration
    padding: 20px 1% 0 1%
    & /deep/ .el-tabs__item
      background: white
    & /deep/ .el-table__body-wrapper
      overflow: visible
    & /deep/ .el-table__row
      > td:nth-child(7)
        color: orange
        font-weight: bold
    & /deep/ .cell .el-button--default
      float: left
      color: #3C8DBC
      font-weight: bold
    .el-dialog__wrapper
      .el-dialog__body
        img
          float: left
          width: 150px
          height: 150px
          &:hover
            cursor: pointer
        .el-col-24
          &.refuse-reason
            margin-bottom: 10px
          margin-top: 5px
          .el-select, .el-button
            width: 100%
</style>



